import React, { Component } from "react"
import { Link } from "gatsby"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { connect } from "react-redux";
import { userActions } from '../../redux/actions/';

import { Title, Subtitle, Field, Label, Control, Input, Button } from 'bloomer';

class UserFormPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      data: {
        name: '',
        email: ''
      }
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    const { data } = this.state
    this.setState({
      data: {
        ...data,
        [e.target.name]: e.target.value
      }
    })
  }

  handleSubmit(e) {
    e.preventDefault()
    let data = this.state.data
    this.props.dispatch(userActions.save(data, this.props.id))
  }

  componentDidMount() {
    this.props.dispatch(userActions.get(this.props.id))
  }

  static getDerivedStateFromProps(props, state) {
    let newState = state

    if (props.user.single && state.user !== props.user.single){
      let user = props.user.single
      newState = {
        ...newState,
        user,
        data: {
          name: user.name,
          email: user.email
        }
      }
    }

    return newState
  }

  render() {
    const { data } = this.state
    console.log(data)
    return (
      <Layout>
        <SEO title="Edit data user"/>
        <Title>User</Title>
        <Subtitle>Edit Data</Subtitle>
        <form
          method="post"
          onSubmit={e => {
            this.handleSubmit(e)
          }}
        >
        <Field>
          <Label>Name</Label>
          <Control>
              <Input
              type="text"
              name="name"
              value={data.name}
              onChange={this.handleChange}
              placeholder='Name' />
          </Control>
        </Field>
        <Field>
          <Label>Email</Label>
          <Control>
              <Input
              type="text"
              name="email"
              value={data.email}
              onChange={this.handleChange}
              placeholder='Email' />
          </Control>
        </Field>
        <Field isGrouped>
          <Control>
            <Button isColor='primary' type='submit'>Submit</Button>
          </Control>
          <Control>
            <Link to="/user" className="button is-link">Back</Link>
          </Control>
        </Field>
        </form>
      </Layout>
    )
  }

}

const mapStateToProps = ({ user }) => {
  return { user }
}

export default connect(mapStateToProps)(UserFormPage);
